#footer .navbar-nav{
    -webkit-flex-direction: row;
    -ms-flex-direction: row;
    flex-direction: row;
}

#footer li {
    list-style: none;
}

#footer p {
    font-size: 10px;
}
#footer a>img {
    transition: height .5s;
    transition: width .5s;
}
#footer a>img:hover {
    margin-top: -10px;
    height: 50px;
    width: 50px;
}