html,
body,
header,
.view {
    height: 100%;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.font-turquoise {
  color: #40c6cc;
}
.font-black {
  color: #000000;
}

.font-white {
  color: #ffffff;
}

.bg-faded {
  background-color: #ffffff3b;

}

.bg-turquoise {
  background-color: #9AD7D9 !important; 
}

.bg-turquoise-d {
  background-color: #40c6cc !important;
}

.bg-black {
  background-color: #000000; 
}
.bg-grey {
  background-color: #E4E4E4;
}

.text-muted {
  color: #6c757d66;
}

.col-centered {
  float: none;
  margin-right: auto;
  margin-left: auto;
}

.card {
  border: none !important;
}

.zoom {
  transition: 0.5s !important;
}

.zoom:hover {
  transform: scale(1.1);
}

.rotate {
  transition: 0.5s !important;
}

.rotate:hover {
  transform: rotate(360deg);
}

.white-bullet li::before {
  content: "\2022";
  color: white;
  font-weight: bold;
  font-size: 21px;
}

.list-unstyled {
  list-style: none !important;
  
}

ul.dashed {
  list-style-type: none;
  padding-left: 0;
}

ul.dashed > li:before {
  content: "-";
  margin-right: 5px;
}

.fade-in {
  -webkit-animation: fadein 2s; /* Safari, Chrome and Opera > 12.1 */
     -moz-animation: fadein 2s; /* Firefox < 16 */
      -ms-animation: fadein 2s; /* Internet Explorer */
       -o-animation: fadein 2s; /* Opera < 12.1 */
          animation: fadein 2s;
}

@keyframes fadein {
  from { opacity: 0; }
  to   { opacity: 1; }
}

/* Firefox < 16 */
@-moz-keyframes fadein {
  from { opacity: 0; }
  to   { opacity: 1; }
}

/* Safari, Chrome and Opera > 12.1 */
@-webkit-keyframes fadein {
  from { opacity: 0; }
  to   { opacity: 1; }
}

/* Internet Explorer */
@-ms-keyframes fadein {
  from { opacity: 0; }
  to   { opacity: 1; }
}

/* Opera < 12.1 */
@-o-keyframes fadein {
  from { opacity: 0; }
  to   { opacity: 1; }
}

.zoom-in {
  -webkit-animation: bummer 1s;
  animation: bummer 1s;
  -webkit-transform: scale(.5,.5); 
  transform: scale(.5,.5);
  -webkit-animation-fill-mode: forwards;
  animation-fill-mode: forwards
}

@-webkit-keyframes bummer {
  100% {
      -webkit-transform: scale(1,1); 
  }
}

@keyframes bummer {
  100% {
      transform: scale(1,1); 
  }
}

.font-slim {
  font-weight: 200;
}